body {
  line-height: 24px;
  font-size: 14px;
  overflow: auto;
  font-family: "Fedra Sans Bold";
}

body, p, h1, h2, h3, h4, h5, h6 {
  margin: 0px;
  padding: 0px;
}

ul {
  list-style: none;
  margin: 0px;
  padding: 0px;
}

a {
  text-decoration: none;
}

a:hover, a:focus {
  text-decoration: none;
  outline: none;
  -webkit-box-shadow: none;
  box-shadow: none;
}

.row.m0 {
  padding: 0px;
  margin: 0px;
}

/* Clear floats after image containers */
.row::after {
  content: "";
  clear: both;
  display: table;
}

.column {
  float: left;
  width: 25%;
  padding: 5px;
  text-align:center;
}
.clear-all{ clear:both;}


.col-md-offset-right-1 {
  margin-right: 8.33333333%;
}

.grid-sizer {
  width: calc(100% / 3);
}

.display_table {
  display: table;
  width: 100%;
}

.display_table_row {
  display: table-row;
}

.display_table_cell {
  display: table-cell;
  float: none !important;
}

  #success {
    display: none;
  }

  #error {
    display: none;
  }


  @media (min-width: 768px){
	.fullwidth{ width:92%;}
	.navbar-brand{margin-left : -56px !important; }

	.navbar-right{margin-right : -51px !important; }
	.pull-left-f{margin-left : -56px !important; }
	.pull-right-f{margin-right : -30px !important; }
	.toph4{
		 margin-bottom: 22px;
	}
	.fullleftdiv{ border-right: 1px solid  #8207ff; }
}

@media (max-width: 991px){
    .service_item {
        max-width: 360px;
        margin: 0px auto 30px;
    }
    .our_service_area .col-md-4:last-child .service_item{
        margin-bottom: 0px;
    }
    .map_area .mapBox {
        position: relative !important;
    }

}
